import React, { useState, useEffect } from 'react';
import firebase from '../firebase';

function AddMoney(props) {
    
     //This is token from Login page, to see which user is logged in
    const liveUser = localStorage.getItem("loggedInUser");
    const transactionDate = new Date().toDateString();

    const[tranStatus, setTranStatus] =  useState(false);
    const[addDetails, setAddDetails] = useState({
        amount:'',
        cardNumber:'',
        expiryDate:'',
        CVV:'',
        nameOnCard:'',
        note:'',
        username:liveUser,
        date:transactionDate
    });

    const statementDetails = {
        date:transactionDate,
        pType:"Deposit",
        accountNumber:false,
        benificiaryCardNumber:addDetails.cardNumber,
        userNote:addDetails.note,
        sentAmount:"0",
        addAmount:addDetails.amount,
    };

    function handleOnChange(e) {
        const {name, value} = e.target;
        setAddDetails(prev => {
            return {
                ...prev,
                [name]: value
            }
        });
    }

    
    useEffect(()=>{

        //const abortCont = new AbortController();
        const AddMoneyRef = firebase.database().ref("Transaction/AddMoneyTransaction/"+liveUser);
        const statementRef = firebase.database().ref("Transaction/AccountStatement/"+liveUser);

            async function databaseQuery (request, response) {
                try {
                        if (tranStatus) {
                            const registerRef = firebase.database().ref("RegistrationTable");
                            const snapshot = await registerRef.once('value');
                            var registrationDetails = await snapshot.val();  
                            var tAmount = 0;
                            for(let id in registrationDetails) {
                             if(liveUser === registrationDetails[id].details.username) {

                                //console.log("db: "+registrationDetails[id].details.amount +" input: "+ addDetails.amount);
                                tAmount = parseInt(registrationDetails[id].details.amount) + parseInt(addDetails.amount);
                                //console.log("tranStatus true snapshot taken : "+ tAmount);
                                
                                await firebase.database().ref('RegistrationTable/'+id+"/details").update({amount: tAmount});
                                addDetails.balance = tAmount;
                                statementDetails.balance = tAmount;

                                statementRef.push(statementDetails);
                                AddMoneyRef.push(addDetails);
                                
                                setTranStatus(false);
                                props.seeAmount();
                                addDetails.amount = '';
                                addDetails.cardNumber = "";
                                addDetails.expiryDate = "";
                                addDetails.CVV = "";
                                addDetails.nameOnCard = "";
                                addDetails.note = "";
                            }
                        }
                    }
                }
                catch (error) {
                    if(error.name === 'AbortError') {
                        //console.log('Fetch Aborted');
                        //console.log(error);
                    }
                    else {
                        setTranStatus(false);
                        //console.log(error);
                    }  
                }  
            }
        databaseQuery();

        return () => setTranStatus(false);
    },[tranStatus, addDetails.amount, addDetails, liveUser, props]);

    function clickAdd(e) {
        e.preventDefault();

        if(addDetails.amount === "" || addDetails.cardNumber === "" || addDetails.expiryDate === "" || addDetails.CVV === "" || addDetails.nameOnCard === "" || addDetails.note === "") {
            alert("Please Enter all the values");
        }
        else if(addDetails.amount <= 0) {
            alert("Amount can not be zero or less than that");
        }
        else if(addDetails.cardNumber.length !== 16) {
            alert("Card number should be 16 digit")
        }
        else if(addDetails.CVV.length !== 3) {
            alert("CVV should be 3 digit")
        }
        else {
            //const data = {addDetails};
            //AddMoneyRef.push(addDetails);
            setTranStatus(true);
            alert("Money Added Successfully");
        }

    }
    return(
        <div className="r-body">
        <div className="r-container">
            <div className="r-title">Add Money</div>
            <form action="#">
                 <div className="r-user-details">
                    <div className="r-input-box">
                         <span className="r-details">Amount</span>
                         <input className="r-input" type="number" name="amount" id="amount" onChange={handleOnChange} value={addDetails.amount} placeholder="Amount" autoComplete="nope"></input>
                     </div>

                     <div className="r-input-box">
                         <span className="r-details">Card Number</span>
                         <input className="r-input" type="number" name="cardNumber" id="cardNumber" onChange={handleOnChange} value={addDetails.cardNumber} placeholder="Card Number" autoComplete="nope"></input>
                     </div>
                     <div className="r-input-box">
                         <span className="r-details">Expiry Date</span>
                         <input className="r-input" type="date" name="expiryDate" id="expiryDate" onChange={handleOnChange} value={addDetails.expiryDate} placeholder="Expiry Date" autoComplete="nope"></input>
                     </div>
                     <div className="r-input-box">
                         <span className="r-details">CVV Number</span>
                         <input className="r-input" type="numebr" name="CVV" id="CVV" onChange={handleOnChange} value={addDetails.CVV} placeholder="CVV Number" autoComplete="nope"></input>
                     </div>
                     <div className="r-input-box">
                         <span className="r-details">Name on Card</span>
                         <input className="r-input" type="text" name="nameOnCard" id="nameOnCard" onChange={handleOnChange} value={addDetails.nameOnCard} placeholder="Name On card" autoComplete="nope"></input>
                     </div>
                     <div className="r-input-box">
                         <span className="r-details">Note</span>
                         <input className="r-input" type="text" name="note" id="note" onChange={handleOnChange} value={addDetails.note} placeholder="Note" autoComplete="nope"></input>
                     </div>

                 </div>
                 <div className="r-button">
                     <input type="submit" name="" onClick={clickAdd} value="Add Money"/>
                 </div>
                 
            </form>
        </div>
        
     </div>
    );
}

export default AddMoney;