import React, {useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
import firebase from '../firebase'

function Registration() {
    
    //check the input of username and compare with other usernames in the database and store into this dbUser
    const [dbUser, setDbUSer] = useState();
    //Check the date if its above 18 then store true value 
    const [validDate, setValidDate] = useState(false);
    //this value will store all the account numbers from the database to use it further
    const [accountNumber, setAccountNumber] = useState([]);
    //This is used to trigger useEffect and clean it too
    const [status, setStatus] = useState(true);

    //store the details of new user
    const [details, setDetails] = useState({
        accType: '',
        name:'',
        phoneNumber:'',
        DOB:'',
        address:'',
        citizenship:'',
        maritalStatus:'',
        occupation:'',
        aadharCard:'',
        panCard:'',
        username:'',
        password:'',
        gender:'',
        amount:'',
        accNumber:''
    });

    //it'll keep track on user input and store into the current state
    function handleChange(e) {
        const {name, value} = e.target; 
         setDetails(prev => {
            return {
                ...prev,
                [name]: value
            }
        });
        
    }

    //databse query
    const registerRef = firebase.database().ref("RegistrationTable");

    //will run when user type his/her username and this will check the uniqueness
    useEffect(()=>{
        const registerRef = firebase.database().ref("RegistrationTable");
        //if status will change this will execute 
                
                if (status){
                    //console.log('into check date');
                    //this will check the user is above 18 or not
                    var value = document.getElementById('DOB').value;
                    var d = new Date(value);
                    var year = d.getFullYear();
                    var month = d.getMonth();
                    var date = d.getDate();
                    var validYear = parseInt(year) + 18;

                    var now = new Date();

                    if(now.getFullYear() > validYear){
                        setValidDate(true);
                    }
                    else if(now.getFullYear() === validYear) {
                        if(now.getMonth() > month) {
                            setValidDate(true);
                    }
                    else if(now.getMonth() === month) {
                        if (now.getDate() > date) {
                            setValidDate(true);
                        }
                        else if(now.getDate() === date){
                            setValidDate(false);
                        }
                        else {
                            setValidDate(false);
                        }
                    }
                    else {
                        setValidDate(false);
                    }
                    }
                    else {
                        setValidDate(false);
                    }
                }
                    
                    //check username
                    var registrationDetails = '';
                async function databaseQuery (request, response) {
                    try {
                        if (status) {
                            //console.log('into databasequery');
                            const snapshot = await registerRef.once('value');
                            registrationDetails = await snapshot.val();

                            const dbUser = [];
                            const accountNumber = [];
                            for(let id in registrationDetails) {
                            accountNumber.push(registrationDetails[id].details.accNumber);
                            if(details.username === registrationDetails[id].details.username) {
                                dbUser.push(registrationDetails[id].details.username);
                                }
                            }
                            setAccountNumber(accountNumber);
                            setDbUSer(dbUser);
                            }
                    }
                    catch (error) {
                        //console.log(error);
                    }
                }
    databaseQuery();

    //cleaning function
    return () => setStatus(false);
    
    }, [status, details.username, details.DOB])


    //this function will run when use click on the register button
    async function registerCustomer(e) {
        setStatus(true);
        //console.log('into set true from register customer function');
        if(details.accType === "" || details.name === "" || details.phoneNumber === "" || details.DOB === "" || details.address === "" || details.citizenship === "" || details.maritalStatus === "" || details.occupation === "" || details.aadharCard === "" || details.panCard === "" || details.username === "" || details.password === "" || details.gender === "" || details.amount === "" ) {
            alert("Please fill all the details");
        }
        else if(details.username === dbUser[0]) {
                    alert("Please Choose Different Username");
                }

        else if (details.phoneNumber.length < 10) {
                    alert("Enter Valid Phone number");
                }
        else if (validDate === false) {
            alert("Your age should be more than 18 years");
        }
        else if (details.aadharCard.length !== 12) {
            alert("Enter Valid Aadhar Card Number, 12 digits required");
        }
        else if (details.panCard.length !== 10) {
            alert("Enter Valid Pan Card Number, 10 digits required");
        }
        else if (details.password.length < 7) {
            alert("Password length should be more than 7");
        }
        else if (details.amount < 5000) {
            alert("Amount should be more than Rs. 5000/-");
        }
         else{
                try {
                    var flag = false;
                    while(flag === false) {
                        var randomNumber = Math.floor(Math.random()*1000000000000 + 1);
                        var newAccountNumber = randomNumber + "7875";
                        const dbAccountNumbers = await accountNumber;
                        if(dbAccountNumbers.indexOf(newAccountNumber) === -1) {
                            flag = true;
                            details.accNumber = newAccountNumber;
                            const data = {details};
                            registerRef.push(data);
                            alert("Successfully Registered");
                            alert("Please Login");
                            }
                        }
                }
                catch (error) {
                    //console.log(error);
                }
            }
           
        e.preventDefault();
    }


    return(
        <div className="r-body">
           <div className="r-container">
               <div className="r-title">Registration</div>
               <form action="#">
                    <div className="r-user-details">

                        <div className="r-input-box">
                            <span className="r-details">Account Type</span>
                            <select onChange={handleChange} value={details.accType} className="r-option" name="accType" id="accType" required>
                                <option value="" defaultValue disabled>Select Account Type</option>
                                <option value="Savings">Savings</option>
                                <option value="Current">Current</option>
                            </select>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Full Name</span>
                            <input onChange={handleChange} value={details.name} name="name" className="r-input" type="text" placeholder="Enter Full Name" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Phone Number</span>
                            <input onChange={handleChange} value={details.phoneNumber} name="phoneNumber" className="r-input" type="number" placeholder="Phone Number" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Date of Birth</span>
                            <input onChange={handleChange} value={details.DOB} name="DOB" id="DOB" className="r-input" type="date" placeholder="Date of Birth" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Address</span>
                            <input onChange={handleChange} value={details.address} name="address" className="r-input" type="text" placeholder="Address" autoComplete="nope" required></input>
                        </div>
                        
                        <div className="r-input-box">
                            <span className="r-details">Citizenship</span>
                            <select onChange={handleChange} value={details.citizenship} className="r-option" name="citizenship" id="citizenship" required>
                                <option value="" defaultValue disabled>Select Country</option>
                                <option value="India">India</option>
                                <option value="Other">Other</option>
                            </select>
                        </div>
                        
                        <div className="r-input-box">
                            <span className="r-details">Marital Status</span>
                            <select onChange={handleChange} value={details.maritalStatus} className="r-option" name="maritalStatus" id="maritalStatus" required>
                                <option value="" defaultValue disabled>Select Status</option>
                                <option value="Single">Single</option>
                                <option value="Married">Married</option>
                                <option value="Divorsed">Divorsed</option>
                            </select>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Occupation</span>
                            <select onChange={handleChange} value={details.occupation} className="r-option" name="occupation" id="occupation" required>
                                <option value="" defaultValue disabled>Select Occupation</option>
                                <option value="Student">Student</option>
                                <option value="Employee">Employee</option>
                                <option value="Business">Business</option>
                            </select>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Aadhar Card Number</span>
                            <input onChange={handleChange} value={details.aadharCard} name="aadharCard" className="r-input" type="number" placeholder="Enter your Aadhar card number" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Pan Card Number</span>
                            <input onChange={handleChange} value={details.panCard} name="panCard" className="r-input" type="name" placeholder="Enter your Pan card number" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Username</span>
                            <input onChange={handleChange} value={details.username} name="username" className="r-input" type="text" placeholder="Username" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Password</span>
                            <input onChange={handleChange} value={details.password} name="password" className="r-input" type="password" placeholder="Password" autoComplete="nope" required></input>
                        </div>

                        <div className="r-input-box">
                            <span className="r-details">Enter Amount to credit on your account</span>
                            <input onChange={handleChange} value={details.amount} name="amount" className="r-input" type="number" placeholder="Amount should be greater than 5000" autoComplete="nope" required></input>
                        </div>
                    </div>

                    <div className="r-gender-details">

                        <input type="radio" name="gender" id="dot-1" checked={details.gender === "Male"} onChange={handleChange} value="Male"/>
                        <input type="radio" name="gender" id="dot-2" checked={details.gender === "Female"} onChange={handleChange} value="Female"/>
                        <input type="radio" name="gender" id="dot-3" checked={details.gender === "PNS"} onChange={handleChange} value="PNS"/>

                        <span className="r-gender-title">Gender</span>
                        <div className="r-catagory">
                            <label htmlFor="dot-1">
                                <span className="r-dot one"></span>
                                <span className="r-gender">Male</span>
                            </label>
                            <label htmlFor="dot-2">
                                <span className="r-dot two"></span>
                                <span className="r-gender">Female</span>
                            </label>
                            <label htmlFor="dot-3">
                                <span className="r-dot three"></span>
                                <span className="r-gender">Prefer Not to Say</span>
                            </label>
                        </div>
                    </div>

                    <div className="r-button">
                        <input type="submit" name="" value="Register" id="registerButton" onClick={registerCustomer}/>
                    </div>
                    <div className="r-reg-btn">
                        <Link to="/" ><input type="submit" name="" value="Log In"/></Link>
                    </div>
                    
               </form>
           </div>
           
        </div>
    );
}

export default Registration;