import React, {useEffect, useState} from 'react';
import logo from './bank.png';
import firebase from '../firebase';

function Statement(props) {

    const liveUser = localStorage.getItem("loggedInUser");
    const date = new Date().toDateString();

    //to clean the useEffect
    const [fetch, setFetch] = useState(true);

    const [liveUserAccountStatement, setLiveUserAccountStatement] = useState([]);

    

    //This useEffect will always run ehrn page loads 
    useEffect(()=>{
        async function fetchUserStatement() {
            const statementRef = firebase.database().ref("Transaction/AccountStatement/"+liveUser);
            //dbRef.on('value', (snapshot) => {
                const snapshot = await statementRef.once('value');
                const accountStatement = await snapshot.val();
                
                const liveUserAccountStatement = [];
                for(let id in accountStatement) {
                    liveUserAccountStatement.push(accountStatement[id]);
                }
                setLiveUserAccountStatement(liveUserAccountStatement);
            //});
        }    
        fetchUserStatement();
        setFetch(false);    
    }, [liveUser]);

   

    return(
            <div className="statement-container">
            <div className="s-title top">Account Statement</div>
            <div className="s-header">
                <div className="s-heading-left">
                    <img className="s-logo" src={logo}/>
                    <div className="s-title">New India Bank</div>
                    <div className="s-bank-address">Modern Building, 128, Wodehouse Road, Colaba, Mumbai, Maharashtra 400005, India</div>
                </div>

                {/* <div className="s-heading-right">
                    <div className="s-userDetails"><span>Date : </span>{date}</div>
                    <div className="s-userDetails"><span>Account Number : </span>{props.accountNumber}</div>
                    <div className="s-userDetails"><span>Name: </span>{props.name}</div>
                    <div className="s-userDetails"><span>Address: </span>{props.address}</div>
                </div> */}
            </div>
            <div className="s-table" >
            <table className="customers">
                <thead>
                <tr>
                    <th>Date</th>
                    <th>Description</th>
                    <th>Withdrawals</th>
                    <th>Deposits</th>
                    <th>Balance</th>
                </tr>
                </thead>
                <tbody>
                {liveUserAccountStatement ? liveUserAccountStatement.map((data, key) => {
                        return<tr key={key}>
                        <td>{data.date}</td>
                        <td>{data.pType} {data.accountNumber ? data.accountNumber : data.benificiaryCardNumber} - {data.userNote}</td>
                        <td>{data.sentAmount}/-</td>
                        <td>{data.addAmount}/-</td>
                        <td>{data.balance}/-</td>
                        </tr>
                }) : ""}
                </tbody>

                
            </table>
            </div>
            </div>
    );
}

export default Statement;