import React, { useEffect, useState } from 'react';
import firebase from '../firebase';

function SendMoney(props) {

    //This is token from Login page, to see which user is logged in
    const liveUser = localStorage.getItem("loggedInUser");
    const transactionDate = new Date().toDateString();

    //check status of transaction
    const[tranStatus, settranStatus] = useState(false);
    const[sendDetails, setSendDetails] = useState({
        bName:'',
        bAccNumber:'',
        amount:'',
        bPhoneNumber:'',
        cBAccNumber:'',
        pType:'',
        note:'',
        user:liveUser,
        date:transactionDate
    });

    const statementDetails = {
        date:transactionDate,
        pType:"Withdrawal",
        accountNumber:sendDetails.bAccNumber,
        benificiaryCardNumber:false,
        userNote:sendDetails.note,
        sentAmount:sendDetails.amount,
        addAmount:"0",
    };

    function handleOnchange(e) {
        const{name, value} = e.target;
        setSendDetails( prev => {
            return {
                ...prev,
                [name]: value
            }
        });
    }

    const sendMoneyRef = firebase.database().ref("Transaction/SendMoneyTransaction/"+liveUser);
    const statementRef = firebase.database().ref("Transaction/AccountStatement/"+liveUser);
    useEffect(()=>{

        //const abortCont = new AbortController();
            async function databaseQuery (request, response) {
                try {
                        if (tranStatus) {
                            const registerRef = firebase.database().ref("RegistrationTable");
                            const snapshot = await registerRef.once('value');
                            var registrationDetails = await snapshot.val();  
                            var tAmount = 0;
                            
                            for(let id in registrationDetails) {
                             if(liveUser === registrationDetails[id].details.username) {
                                //console.log(registrationDetails[id].details.amount +" "+ sendDetails.amount);
                                tAmount = registrationDetails[id].details.amount - sendDetails.amount;
                                //console.log("tranStatus true snapshot taken condition in and: "+ tAmount);
                                
                                await firebase.database().ref('RegistrationTable/'+id+"/details").update({amount: tAmount});
                                sendDetails.balance = tAmount;
                                statementDetails.balance = tAmount;

                                statementRef.push(statementDetails);
                                sendMoneyRef.push(sendDetails);
                                settranStatus(false);
                                props.seeAmount();
                                sendDetails.bName = '';
                                sendDetails.bAccNumber = '';
                                sendDetails.cBAccNumber = '';
                                sendDetails.bPhoneNumber = '';
                                sendDetails.note = '';
                                sendDetails.amount = '';
                            }
                        }
                    }
                }
                catch (error) {

                    if(error.name === "AbortError") {
                        //console.log("Fetch Aborted");
                    }
                    else {
                        settranStatus(false);
                        //console.log(error);
                    }
                }  
            }
        databaseQuery();

        return () => settranStatus(false);
    },[tranStatus, sendDetails.amount, liveUser, props]);



    //send Money
    function clickSend(e) {
        if(sendDetails.bName === "" || sendDetails.bAccNumber === "" || sendDetails.cBAccNumber === "" || sendDetails.amount === "" || sendDetails.note === "" || sendDetails.pType === "") {
            alert("Please Enter all the details");
        }
        else if(sendDetails.bAccNumber.length !== 16 ){
            alert("Please enter valid account number, should be 16 digit");
        }
        else if(sendDetails.cBAccNumber !== sendDetails.bAccNumber) {
            alert("Both account number input field should match");
        }
        else if(sendDetails.amount <= 0) {
            alert("Amount Can not be 0");
        }
        else {
            //const data = {sendDetails};
            //sendMoneyRef.push(sendDetails);
            settranStatus(true);
            alert("Money Sent");
            
        } 

        e.preventDefault();
    }


    return(
        <div className="r-body">
        <div className="r-container">
            <div className="r-title">Send Money</div>
            <form action="#">
                 <div className="r-user-details">

                     <div className="r-input-box">
                         <span className="r-details">Beneficiary name</span>
                         <input className="r-input" type="text" placeholder="Beneficiary name" name="bName" value={sendDetails.bName} onChange={handleOnchange} autoComplete="nope"></input>
                     </div>

                     <div className="r-input-box">
                         <span className="r-details">Beneficiary Account Number</span>
                         <input className="r-input" type="number" placeholder="Beneficiary Account Number" name="bAccNumber" value={sendDetails.bAccNumber} onChange={handleOnchange} autoComplete="nope"></input>
                     </div>
                     <div className="r-input-box">
                         <span className="r-details">Confirm Account Number</span>
                         <input className="r-input" type="number" placeholder="Confirm Account Number" name="cBAccNumber" value={sendDetails.cBAccNumber} onChange={handleOnchange} autoComplete="nope"></input>
                     </div>

                     <div className="r-input-box">
                         <span className="r-details">Amount</span>
                         <input className="r-input" type="number" placeholder="Amount" name="amount" value={sendDetails.amount} onChange={handleOnchange} autoComplete="nope"></input>
                     </div>

                     <div className="r-input-box">
                         <span className="r-details">Phone Number</span>
                         <input className="r-input" type="numeber" placeholder="Phone Number" name="bPhoneNumber" value={sendDetails.bPhoneNumber} onChange={handleOnchange} autoComplete="nope"></input>
                     </div>

                     <div className="r-input-box">
                         <span className="r-details">Note</span>
                         <input className="r-input" type="text" placeholder="Note" name="note" value={sendDetails.note} onChange={handleOnchange} autoComplete="nope"></input>
                     </div>

                 </div>

                 <div className="r-gender-details">

                     <input type="radio" name="pType" id="NEFT" value="NEFT" onChange={handleOnchange}/>
                     <input type="radio" name="pType" id="RTGS" value="RTGS" onChange={handleOnchange}/>
                     <input type="radio" name="pType" id="IMPS" value="IMPS" onChange={handleOnchange}/>

                     <span className="r-gender-title">Payment Type</span>
                     <div className="r-catagory">
                         <label htmlFor="NEFT">
                             <span className="r-dot one"></span>
                             <span className="r-gender">NEFT</span>
                         </label>
                         <label htmlFor="RTGS">
                             <span className="r-dot two"></span>
                             <span className="r-gender">RTGS</span>
                         </label>
                         <label htmlFor="IMPS">
                             <span className="r-dot three"></span>
                             <span className="r-gender">IMPS</span>
                         </label>
                     </div>
                 </div>

                 <div className="r-button">
                     <input type="submit" name="" onClick={clickSend} value="Send Money"/>
                 </div>
                 
            </form>
        </div>
        
     </div>
    );
}

export default SendMoney;